/* src/styles.css */
/* Navbar Styles */
nav {
    background-color: #333;
    padding: 10px 20px;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-around;
}

nav ul li {
    margin: 0 10px;
}

nav ul li a {
    color: white;
    text-decoration: none;
    font-weight: bold;
}

nav ul li a:hover {
    color: #ffa500;
}

.navbar-welcome {
  color: mediumseagreen;
  font-weight: bold;
}

nav ul li a.active-link {
    color: #ffa500; /* Orange color for active link */
    text-decoration: underline; /* Optional: Add underline to highlight */
}

/* General Styles */
body {
    margin: 0;
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    color: #333;
}

h1, h2, h3, h4, h5, h6 {
    color: #333;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

/* Form Styles */
form {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

form input, form select, form textarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

form button {
    background-color: #333;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

form button:hover {
    background-color: #ffa500;
}


/* src/Home.css */
.home-background {
    background-image: url('https://thumbs.wbm.im/pw/medium/14d7406883d9696a3b8572661d76fe30.avif');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    padding: 20px;
    box-sizing: border-box;
}

.home-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 30px;
    background-color: rgba(56, 55, 55, 0.5); /* Dark neutral background with 70% opacity */
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    text-align: center;
    color: white; /* Ensure the text is visible on the darker background */
}

.home-container h1 {
    font-size: 2em;
    line-height: 1.6;
    color: white;
}

.home-container button {
    background-color: #FFA500; /* A bright, inviting color */
    color: white; /* White text for contrast */
    border: none; /* Remove default border */
    padding: 12px 24px; /* Add padding for a comfortable size */
    border-radius: 5px; /* Rounded corners */
    font-size: 16px; /* Increase font size for readability */
    font-weight: bold; /* Make text bold */
    cursor: pointer; /* Pointer cursor to indicate it's clickable */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    transition: background-color 0.3s ease, transform 0.3s ease; /* Smooth transition for hover effects */
}

.home-container button:hover {
    background-color: #FF8C00; /* Slightly darker shade on hover */
    transform: translateY(-2px); /* Subtle lift effect on hover */
}

.home-container button:active {
    background-color: #E07A00; /* Darker shade when pressed */
    transform: translateY(0); /* Reset the lift effect */
}


