/* Adopt Pet Page styles */
.form-container {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  }
  
  .form-container h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .form-container form {
    display: flex;
    flex-direction: column;
  }
  
  .form-container input,
  .form-container select,
  .form-container textarea {
    margin-bottom: 15px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .form-container button {
    padding: 10px 20px;
    font-size: 18px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .form-container button:hover {
    background-color: #45a049;
  }
  
  .selected-pet-container {
    text-align: center;
    margin-top: 20px;
  }
  
  .selected-pet-container img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  }  
  

  /*View Pets Page Styles*/
  .viewPets-container {
    text-align: center;
    padding: 20px;
  }
  
  .pet-section {
    margin-bottom: 40px;
  }
  
  .pet-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
  }
  
  .pet-grid img {
    width: 200px; /* Standard width */
    height: 200px; /* Standard height */
    object-fit: cover; /* Ensures the image covers the entire area without distorting */
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .pet-grid img:hover {
    transform: scale(1.1); /* Scale up the image on hover */
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3); /* Increase shadow depth on hover */
  }
  
  
/*Contact Info Page*/
.contact-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 30px;
    background-color: rgba(168, 138, 138, 0.7);
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .contact-container h1 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .contact-container p {
    font-size: 18px;
    margin-bottom: 30px;
    color: #555;
  }
  
  .contact-details {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .contact-item {
    flex: 1;
    min-width: 200px;
    margin: 10px 20px;
  }
  
  .contact-item h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .contact-item p {
    font-size: 16px;
    color: #333;
  }
  
  .contact-map {
    margin-top: 40px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  