/* Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Dark semi-transparent background */
    backdrop-filter: blur(5px); /* Apply blur to the background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    max-width: 400px; /* Restrict width to keep the form from being too wide */
    width: 100%;
    position: relative;
    z-index: 1001;
    box-sizing: border-box; /* Ensure padding does not affect width */
}

.modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #333;
}

.modal-close:hover {
    color: #ffa500; /* Optional: Change color on hover */
}
